<template>
  <div id="header">
    <!--标题图片-->
      <img class="title1" src="../../../assets/login_images/title1.png" alt="">
    <!--右上角登录状态栏-->
    <div class="statusColumn">
      <span><Icon type="md-person" style="font-size: 16px;margin-right:5px;color:#000;margin-top: -2px"/>欢迎您，<strong>系统管理员</strong></span>
      <span>
        <router-link to="/login" @click.native="delLogin">
          <Icon type="ios-close-circle" style="font-size: 16px;margin-right:5px;color:#000;margin-top: -2px"/>退出系统
        </router-link>
      </span>
    </div>
    <!--右下角导航-->
    <ul class="nav">
      <li v-for="(item,index) in data" :key="index" style="position: relative" @click="littleNavState=item.meta.id;theIndex = index" ref="nav">
        <router-link :to="item.url">
          <p class="navP" :class="{navActive :navActiveState === item.meta.id || littleNavState === item.meta.id}">
            <strong :style="{backgroundPosition:item.backPosition }"></strong>
            <span>{{item.name}}</span>
          </p>
        </router-link>
        <div class="littleNav" ref="littleNav" v-show="littleNavState === item.meta.id && item.children.length !== 0">
          <div v-for="(item2,index) in item.children" :key="index" @click.stop="littleNavState = -1;theIndex = -1">
            <router-link :to="item2.url">
              <p class="littleNavP">
                <strong :style="{backgroundPosition:item2.backPosition }"></strong>
                <span>{{item2.name}}</span>
              </p>
            </router-link>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Nav',
  data () {
    return {
      data: [
        {
          name: '" 一张图 " 数据中心',
          url: '/main/dataCenter',
          backPosition: '0 210px',
          children: [],
          meta: {
            id: 1
          }
        },
        {
          name: '数据调查台账',
          url: '#',
          backPosition: '0 180px',
          meta: {
            id: 2
          },
          children: [
            {
              name: '数据调查表单',
              url: '/main/dataCollection/editTable',
              backPosition: '0 -60px',
              meta: {
                id: 3
              }
            },
            {
              name: '数据调查信息',
              url: '/main/dataCollection/dataTable',
              backPosition: '0 0',
              meta: {
                id: 4
              }
            }
          ]
        },
        {
          name: '勘界定标档案',
          url: '#',
          backPosition: '0 150px',
          meta: {
            id: 5
          },
          children: [
            {
              name: '拐点坐标',
              url: '/main/circlesMark/inflectionPoint',
              backPosition: '0 -40px',
              meta: {
                id: 6
              }
            },
            {
              name: '预设界碑界桩',
              url: '/main/circlesMark/presetTablet',
              backPosition: '0 -120px',
              meta: {
                id: 7
              }
            },
            {
              name: '实际界碑界桩',
              url: '/main/circlesMark/actualTablet',
              backPosition: '0 -140px',
              meta: {
                id: 8
              }
            },
            {
              name: '预设标识标牌',
              url: '/main/circlesMark/presetSignBoard',
              backPosition: '0 -80px',
              meta: {
                id: 9
              }
            },
            {
              name: '实际标识标牌',
              url: '/main/circlesMark/signBoard',
              backPosition: '0 -100px',
              meta: {
                id: 10
              }
            }
          ]
        },
        {
          name: '项目准入核查',
          url: '/main/projectApproval/proApp',
          backPosition: '0 120px',
          children: [],
          meta: {
            id: 11
          }
        },
        {
          name: '红线功能评估',
          url: '#',
          backPosition: '0 90px',
          meta: {
            id: 14
          },
          children: [
            {
              name: '重要性评估',
              url: '/main/dynamicManage/importance',
              backPosition: '0 -20px',
              meta: {
                id: 15
              }
            },
            {
              name: '脆弱性评估',
              url: '/main/dynamicManage/fragility',
              backPosition: '0 -160px',
              meta: {
                id: 16
              }
            }
          ]
        },
        {
          name: '综合监管决策',
          url: '#',
          backPosition: '0 60px',
          children: [
            /* {
              name: '问题点位管理',
              url: '/main/mobilesSupervision/problemSpot',
              backPosition: '0 -20px',
              meta: {
                id: 23
              }
            }, */
            {
              name: '航点管理',
              url: '/main/mobilesSupervision/routeSpot',
              backPosition: '0 -180px',
              meta: {
                id: 24
              }
            }
          ],
          meta: {
            id: 17
          }
        },
        {
          name: '系统管理',
          url: '/main/systemManage/userManage',
          backPosition: '0 30px',
          children: [],
          meta: {
            id: 18
          }
        }
      ],
      /* 路由高亮状态 */
      navActiveState: -1,
      /* 二级路由框展示状态 */
      littleNavState: -1,
      /* 当前点击的导航的index */
      theIndex: -1
    }
  },
  watch: {
    $route (to) {
      this.navActiveState = this.judgeRouter(to.path)
    }
  },
  mounted () {
    document.addEventListener('click', this.littleNavHide)
    this.navActiveState = this.judgeRouter(this.$route.path)
    let arr = []
    if (this.$store.getters.getloginInfor() !== null) {
      for (let item of this.$store.getters.getloginInfor().resource) {
        arr.push(item.id)
      }
    }
    for (let i in this.data) {
      this.data[i].children = this.data[i].children.filter(wes => arr.indexOf(wes.meta.id) !== -1)
    }
    this.data = this.data.filter(wes => arr.indexOf(wes.meta.id) !== -1)
  },
  beforeDestroy () {
    document.removeEventListener('click', this.littleNavHide)
  },
  methods: {
    /* 一级导航router判断 */
    judgeRouter (url) {
      if (url.indexOf('/main/dataCenter') !== -1) {
        return 1
      }
      if (url.indexOf('/main/dataCollection') !== -1) {
        return 2
      }
      if (url.indexOf('/main/circlesMark') !== -1) {
        return 5
      }
      if (url.indexOf('/main/projectApproval') !== -1) {
        return 11
      }
      if (url.indexOf('/main/dynamicManage') !== -1) {
        return 14
      }
      if (url.indexOf('/main/mobilesSupervision') !== -1) {
        return 17
      }
      if (url.indexOf('/main/systemManage') !== -1) {
        return 16
      }
    },
    /* 点击任意地方隐藏二级导航 */
    littleNavHide (e) {
      if (this.littleNavState !== -1) {
        if ((!this.$refs.littleNav[this.theIndex].contains(e.target)) && (!this.$refs.nav[this.theIndex].contains(e.target))) {
          this.littleNavState = -1
        }
      }
    },
    /* 删除登录信息 */
    delLogin () {
      this.$store.commit('setloginInfor', {})
      sessionStorage.removeItem('loginInfo')
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  #header{
    border-bottom:10px solid #545454 ;
    position: relative;
    background: url("../../../assets/main_images/navBack.jpg") no-repeat;
    background-size: 100% 100%;
    .title1{
      height: 25px;
      margin:25px 30px;
    }
    /*右上角登录状态栏样式*/
    .statusColumn{
      background: #fff;
      position: absolute;
      right:0;
      top:0;
      padding: 3px 3px 3px 15px;
      border-left:3px solid #126e9f;
      border-bottom:2px solid #126e9f;
      border-radius:0 0 0 10px;
      img{
        vertical-align:middle;
        width:15px;
        margin:0 12px;
      }
      span{
        vertical-align:middle;
        font-weight: bold;
        font-size: 10px;-webkit-transform:scale(0.9);display:inline-block;
        strong{
          color:#1da6ce
        }
      }
    }
    /*右下角导航样式*/
    .nav{
      background: #f0f0f0;
      border-radius:8px 0 0 0;
      overflow: visible;
      zoom: 1;
      position: absolute;
      right:0;
      bottom:0;
      padding:0 0 0 20px;
      box-shadow: -4px 0 0 0 #126e9f;
      >li{
        float:left;
        padding:5px 10px 0;
        border-right: 1px solid #cdcdcd;
        cursor:pointer;
        .navP{
          border-radius: 5px 5px 0 0;
          padding:3px 8px;
          strong{
            display: inline-block;
            width: 30px;
            height:30px;
            vertical-align:middle;
            margin-right: 10px;
            background: url("../../../assets/main_images/nav.png");
            background-size: 100% auto;
          }
        }
        .navActive{
          border-radius: 5px 5px 0 0;
          background: #1DA6CE;
          color:#fff;
          padding:3px 8px;
          strong{
            width: 30px;
            height:30px;
            background: url("../../../assets/main_images/nav_white.png");
            background-size: 100% auto;
          }
        }
        span{
          font-size: 13px;
          vertical-align:middle;
        }
        .littleNav{
          position: absolute;
          top: 41px;
          left: 0;
          background: #f0f0f0;
          border: 1px #c5c5c5 solid;
          border-top: 0;
          z-index: 53615361;
          width: 100%;
          border-radius: 0 0 3px 3px;
          .littleNavP{
            margin:4px 0;
            padding:8px 10px;
            border-radius: 0;
            strong{
              display: inline-block;
              width: 20px;
              height:20px;
              vertical-align:middle;
              margin-right: 10px;
              background: url("../../../assets/main_images/littleNav.png");
              background-size: 100% auto;
            }
          }
          a.router-link-active>.littleNavP{
            background: #1DA6CE;
            color:#fff;
            strong{
              width: 20px;
              height:20px;
              background: url("../../../assets/main_images/littleNav_white.png");
              background-size: 100% auto;
            }
          }
        }
      }
    }
  }
</style>
